.coach-manager {
    width: 100%;
    height: 100%;
}

.education-icon {
    font-size: 24px;
    font-weight:100;

}


.coach-manager-header {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 100px;
}

.coach-photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.coach-manager-body {
    width: 100%;
    height: 100%;
}

.coach-manager-body-up {
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: column;
}

.coach-manager-body-up-up {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 100px;
}

.coach-manager-body-up-up-left{
    display: flex;
    justify-content: space-around;
}
.coach-manager-body-up-down {
    width: 100%;
    height: 100px;
}

.coach-manager-body-down {
    width: 100%;
    height: 100px;
}

.coach-manager-body-down-up {
    width: 100%;
    height: 100px;
}

.coach-manager-body-down-down {
    width: 100%;
    height: 100px;
}






